import React, { useEffect, useState } from "react";
import Repos from "./Repos";
import Error from "./Error";
import { Link, useParams } from "react-router-dom";

export default function User() {
  const { param } = useParams();
  const [found, setFound] = useState();
  const [data, setData] = useState([]);

  document.title = `MyGit2 - ${param}`;

  useEffect(()=>{
    async function getData() {
        const fetchData = await fetch(`http://api.github.com/users/${param}`);
    
        if (fetchData.status === 200) {
          setData(await fetchData.json());
          setFound(true);
        } else {
          console.log("not found");
          setFound(false);
        }
      }
      getData()
  },[]);

  const {
    avatar_url,
    name,
    login,
    company,
    location,
    created_at,
    public_repos,
    followers,
    following,
    repos_url,
  } = data;

  return (
    <>
      {found === true && (
        <div className="user">
          <div className="top-section">
            <Link className="back-btn" to="/">
              <span className="iconify" data-icon="akar-icons:home"></span>
            </Link>

            <img src={avatar_url} className="avtar" alt="profile img" />
            <h1 className="name">{name}</h1>
            <h2 className="username">@{login}</h2>
            <ul className="info">
              {!company === null && (
                <li>
                  <span
                    className="iconify"
                    data-icon="ic:round-work-outline"
                  ></span>
                  <span className="text">{company}</span>
                </li>
              )}
              {!location === null && (
                <li>
                  <span
                    className="iconify"
                    data-icon="akar-icons:location"
                  ></span>
                  <span className="text">{location}</span>
                </li>
              )}

              <li>
                <span className="iconify" data-icon="uil:calender"></span>
                <span className="text">joined {created_at}</span>
              </li>
            </ul>

            <ul className="brief">
              <li>
                <span className="no">{public_repos}</span>
                <span className="text">REPOSITORIES</span>
              </li>
              <li>
                <span className="no">{followers}</span>
                <span className="text">FOLLOWERS</span>
              </li>
              <li>
                <span className="no">{following}</span>
                <span className="text">FOLLOWING</span>
              </li>
            </ul>
          </div>
          <div className="bottom-section">
            <h1>All Repos</h1>
            <div className="repo-wrapper">
              <Repos repos_url={repos_url} />
            </div>
          </div>
        </div>
      )}

      {found === false && <Error />}
    </>
  );
}